@import url(/Css/normalize.css);

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

:root {
  --primary-color: #2584ff;
  --secondary-color: #00d9ff;
  --accent-color: #ff3400;
  --headings-color: #1b0760;
  --body-color: #918ca4;
  --border-color: #ccc;
  --border-radius: 30px;
  --darker-body: #5c5577;
}

body {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  color: var(--body-color);
  font-size: 2.4rem;
  line-height: 1.5;
  margin: 0;
}

/* Typography */

::selection {
  background: var(--primary-color);
  color: white;
}

h1,
h2,
h3 {
  color: var(--headings-color);
  margin-bottom: 1rem;
  line-height: 1.1;
}

h1 {
  font-size: 7rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
  font-weight: 500;
}

p {
  margin-top: 0;
}

@media screen and (min-width: 1024px) {
  body {
    font-size: 1.8rem;
  }

  h1 {
    font-size: 8rem;
  }

  h2 {
    font-size: 4rem;
  }

  h3 {
    font-size: 2.4rem;
  }
}

/* Links */
a {
  text-decoration: none;
}

#brand {
  padding-left: 2rem;
  padding-top: 1rem;
  height: 10rem;
}

#product {
  width: 700px;
  height: 500px;
}

@media screen and (max-width: 768px) {
  #product {
    width: 500px;
  }
}
@media screen and (max-width: 560px) {
  #product {
    width: 300px;
  }
}

#product-section {
  margin-bottom: 0;
  padding-bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#about-section {
  padding-top: 0;
}

#about {
  margin-top: 0;
}

#meeting {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 1024px) {
  #meeting {
    display: block;
  }
  /* #meeting-left__block {
    flex-basis: 100%;
  }
  #meeting-right__block {
    padding-left: 5rem;
    flex-basis: 100%;
  } */
}

#meeting-left__block {
  flex-basis: 100%;
}
#meeting-right__block {
  padding-left: 5rem;
  flex-basis: 100%;
}

.meeting-text {
  /* margin-top: 5rem; */
}

.link-arrow {
  color: var(--accent-color);
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bold;
}

.link-arrow::after {
  content: '-->';
  margin-left: 5px;
  transition: margin-left 0.15s;
}

.link-arrow:hover::after {
  margin-left: 10px;
}

@media screen and (min-width: 1024px) {
  .link-arrow {
    font-size: 1.5rem;
  }
}
/* badges */

.badge {
  border-radius: 20px;
  color: white;
  font-size: 2rem;
  font-weight: 600;
  padding: 0.5rem 2rem;
  white-space: nowrap;
}

.badge--primary {
  background: var(--primary-color);
}

.badge--secondary {
  background: var(--secondary-color);
}

.badge--small {
  font-size: 1.6rem;
  padding: 0.5rem 1.3rem;
}

@media screen and (mind-width: 1024px) {
  .badge {
    font-size: 1.5rem;
  }
}

.badge--small {
  font-size: 1.2rem;
}

/* lists */

.list {
  list-style: none;
  padding-left: 0;
}

.list--inline .list__item {
  display: inline-block;
  margin-right: 2rem;
  font-weight: 600;
}

.list--tick {
  list-style-image: url(/icons/tick.svg);
  padding-left: 3rem;
  padding-right: 1rem;
  color: var(--headings-color);
}
.list--tick b {
  color: var(--primary-color);
}

.list--tick .list__item {
  padding-left: 0.5rem;
  margin-bottom: 1.5rem;
  font-weight: 600;
}

@media screen and (min-width: 1024px) {
  .list--tick .list__item {
    padding-left: 0rem;
  }
}

/* icons */

.icon {
  width: 40px;
  height: 40px;
}

.icon--primary {
  fill: var(--primary-color);
}

.icon--white {
  fill: #fff;
}

.icon--small {
  width: 30px;
  height: 30px;
}

.icon-container {
  background: #f3f9fa;
  width: 64px;
  height: 64px;
  border-radius: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.icon-container--accent {
  background: var(--accent-color);
}

/* buttons */

.buttons {
  border-radius: 40px;
  cursor: pointer;
  font-weight: 600;
  outline: 0;
  padding: 2rem 4vw;
  text-transform: uppercase;
  font-size: 1.8rem;
  white-space: nowrap;
  margin: 1rem 0;
}

.buttons--primary {
  width: 100%;
  display: inline-block;
  background: white;
  text-align: center;
  color: var(--headings-color);
  border: var(--headings-color) solid 2.5px;
}

.buttons .icon {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
  vertical-align: middle;
}

.buttons--primary:hover {
  background: var(--headings-color);
  color: white;
}

.buttons--secondary {
  color: white;
  background: var(--secondary-color);
  border: 0;
}

.buttons--stretched {
  padding-left: 6rem;
  padding-right: 6rem;
}

.buttons--secondary:hover {
  background: #58e6ff;
}

.buttons--accent {
  border: 0;
  color: white;
  background: var(--accent-color);
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.buttons--accent:hover {
  background: #db2d00;
}

@media screen and (min-width: 1024px) {
  .buttons {
    font-size: 1.5rem;
  }
}

/* inputs */

.input {
  padding: 1.5rem 3.5rem;
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  background: white;
  outline: 0;
  color: var(--headings-color);
  font-size: 2rem;
}

::placeholder {
  color: #a7a4ba;
  border-radius: var(--border-radius);
}

.input-group {
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  display: flex;
}

.input-group .input {
  border: 0;
  flex-grow: 1;
  padding: 1.5rem 4rem;
  width: 0;
}

.input-group .buttons {
  margin: 4px;
}

@media screen and (min-width: 1024px) {
  .input {
    font-size: 1.5rem;
  }
}

/* Cards */

.card {
  border-radius: 7px;
  box-shadow: 0 0 20px 10px #f3f3f3;
  overflow: hidden;
}

.card__header,
.card__body {
  padding: 2rem 3rem;
}

.card--primary .card__header {
  background: var(--primary-color);
  color: white;
}
.card--secondary .card__header {
  background: var(--secondary-color);
  color: white;
}

.card--secondary .badge--secondary {
  background: #02cdf1;
}
.card--primary .badge--primary {
  background: #126de4;
}

/* Plans */

.plan {
  transition: transform 0.2s ease-out;
}
.plan__name {
  margin: 0;
  color: white;
  font-weight: 500;
  font-size: 2.4rem;
}

.plan__price {
  font-size: 6rem;
}

.plan__billing-cycle {
  color: #ddf8ff;
  font-size: 2.4rem;
  font-weight: 300;
  opacity: 0.8;
  margin-right: 1rem;
}

.plan__description {
  font-size: 2rem;
  font-weight: 300;
  letter-spacing: 1px;
  display: block;
}

.plan .list__item {
  letter-spacing: 1px;
  margin-bottom: 2rem;
}

.plan--popular {
  transform: scale(1.1);
}

.plan--popular .card__header {
  position: relative;
}

.plan--popular .card__header::before {
  content: url(/icons/popular.svg);
  width: 20px;
  display: inline-block;
  position: absolute;
  top: -6px;
  right: 5%;
}

.plan:hover {
  transform: scale(1.05);
}

.plan--popular:hover {
  transform: scale(1.1);
}

@media screen and (min-width: 1024px) {
  .plan__name {
    font-size: 1.4rem;
  }

  .plan__price {
    font-size: 5rem;
  }

  .plan__billing-cycle {
    font-size: 1.6rem;
  }

  .plan__description {
    font-size: 1.7rem;
  }
}

/* Media  objects */

.media {
  display: flex;
  margin-bottom: 4rem;
}

.media__title {
  margin-top: 0;
  font-size: 2.4rem;
}

.media__body {
  margin: 0 2rem;
  font-size: 2rem;
}

.media__description {
  font-size: 2rem;
  color: var(--border-color);
}

.media__image {
  margin-top: 1rem;
}

@media screen and (min-width: 1024px) {
  .media__title {
    font-size: 1.8rem;
  }
  .media__description {
    font-size: 1.8rem;
  }
}

/* Quotes */

.quote {
  font-size: 3rem;
  color: var(--darker-body);
  line-height: 1.3;
}

.quote__text {
  font-style: italic;
}

.quote__text::before {
  content: open-quote;
}
.quote__text::after {
  content: close-quote;
}

.quote .quote__author {
  font-family: 'Montserrat', sans-serif;
  font-size: 3rem;
  margin-top: 1rem;
}

.quote__description {
  opacity: 0.4;
  font-size: 2rem;
}

@media screen and (min-width: 1024px) {
  .quote .media__title {
    font-size: 2.4rem;
  }

  .quote .quote__description {
    font-size: 1.6rem;
  }

  .quote .quote__text {
    font-size: 2.4rem;
  }
}

/* Grids */

.grid {
  display: grid;
}

@media screen and (min-width: 768px) {
  .grid--1x2 {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1024px) {
  .grid--1x3 {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* testimonials */

.testimonial .testimonial__image {
  padding: 4rem;
}

.testimonial__image > img {
  width: 100%;
}

.testimonial__image {
  position: relative;
}

.testimonial__image > .icon-container {
  position: absolute;
  top: 56px;
  right: 8.5px;
}

@media screen and (min-width: 768px) {
  .testimonial .quote,
  .testimonial .quote__author {
    font-size: 2.4rem;
    margin-top: 5rem;
  }
}

@media screen and (min-width: 1024px) {
  .testimonial .quote {
    margin-top: 15rem;
  }
}

/* Callouts */

.callout {
  padding: 4rem;
  border-radius: 5px;
  max-width: 980px;
  margin: 0 auto;
  max-width: 580px;
  border-radius: 10px;
}

.callout--primary {
  background: var(--primary-color);
  color: #fff;
}

.callout__heading {
  color: #fff;
  font-size: 3rem;
  margin-top: 0;
}

.callout .buttons {
  justify-self: center;
  align-self: center;
  text-align: center;
}

.callout-signup {
  transform: translateY(15rem);
}

.callout__content {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .callout .grid--1x2 {
    grid-template-columns: 1fr auto;
  }

  .callout__content {
    text-align: left;
  }

  .callout .buttons {
    justify-self: start;
    margin: 0 2rem;
  }

  .callout {
    max-width: 980px;
  }
}

/* collapsibles */

.collapsible__header {
  display: flex;
  justify-content: space-between;
}

.collapsible__heading {
  margin-top: 0;
  font-size: 3rem;
}

.collapsible__chevron {
  transform: rotate(-90deg);
}

.collapsible__content {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: all 0.3s;
}

.collapsible--expanded .collapsible__chevron {
  transform: rotate(0);
}

.collapsible--expanded .collapsible__content {
  opacity: 1;
  max-height: 100vh;
}

/* blocks */
.block {
  --padding-vertical: 6rem;
  padding: 2rem;
}

.block__heading {
  margin-top: 0;
}

.block--dark {
  background: #000;
  color: #7b858b;
}

.block--dark h1,
.block--dark h2,
.block--dark h3 {
  color: white;
}

.block--skewed-right {
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 92%);
  padding-bottom: calc(var(--padding-vertical) + 4rem);
}
.block--skewed-left {
  clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
  padding-bottom: calc(var(--padding-vertical) + 4rem);
}

.block__header {
  text-align: center;
  margin-bottom: 4rem;
  margin-top: 2rem;
}

.container {
  max-width: 1350px;
  margin: 0 auto;
}

/* Navigation Bar */

.nav {
  background: black;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 1rem;
  align-items: center;
}

.nav__list {
  width: 100%;
  margin: 0;
}

.nav__item {
  padding: 0.5rem 2rem;
  border-bottom: 1px solid #222;
  transition: color 0.3s;
}

.nav__item > a {
  color: #807d84;
}

.nav__item > a:hover {
  color: #dcd6e2;
}

.nav__toggler {
  opacity: 0.5;
}
.nav__toggler:hover {
  cursor: pointer;
  transition: box-shadow 0.15s;
}

.nav.collapsible--expanded .nav__toggler {
  opacity: 1;
  box-shadow: 0 0 0 3px #666;
  border-radius: 5px;
}

.nav__brand {
  transform: translatey(5px);
}

@media screen and (min-width: 768px) {
  .nav__toggler {
    display: none;
  }

  .nav__list {
    width: auto;
    display: flex;
    font-size: 1.6rem;
    max-height: 100%;
    opacity: 1;
  }
  .nav__item {
    border: 0;
  }
}

/* hero */

.hero__tagline {
  font-size: 2rem;
  columns: #b9c3cf;
  letter-spacing: 1px;
  margin: 2rem 0 5rem;
}

.hero__image {
  width: 100%;
}

@media screen and (min-width: 768px) {
  .hero {
    padding-top: 0;
  }

  .hero__content {
    text-align: left;
    align-self: center;
  }
}

/* Domain Block */

.block--domain .input-group {
  box-shadow: 0 0 30px 20px #e6ebee;
  border: 0;
  margin: 4rem auto;
  max-width: 670px;
}

.block__domain-prices {
  color: var(--headings-color);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 6rem);
  font-size: 2rem;
  font-weight: 600;
  justify-items: center;
  max-width: 700px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .block__domain-prices {
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    font-size: 1.5rem;
  }
}

/* plans-box */

.block__plans {
  padding-left: 2rem;
  padding-right: 2rem;
  margin-bottom: 3rem;
  margin-top: 3rem;
}

.block--container .card {
  max-width: 500px;
  margin: 0 auto;
}

/* features */

.feature {
  gap: 2rem;
  margin-bottom: 10rem;
}
.feature__heading {
  margin: 1rem 0;
}

.feature__image {
  width: 100%;
}

.feature__header {
  margin-bottom: 4rem;
}

@media screen and (min-width: 768px) {
  .feature:nth-of-type(even) .feature__content {
    order: 2;
  }
}

/* showcase Block */

.block-showcase__image > img {
  width: 100%;
}

.showcase__heading {
  text-align: center;
  color: var(--primary-color) !important;
}

@media screen and (min-width: 768px) {
  .block-showcase .grid {
    grid-template-columns: 50% 50%;
  }

  .block-showcase__image {
    justify-self: end;
  }

  .block-showcase__image > img {
    width: auto;
    max-width: 700px;
  }
}

/* footer */

.footer {
  background: #232323;
  padding-bottom: 3rem;
  padding-top: 20rem;
}

.footer__section {
  padding: 2rem;
  border-bottom: 1px solid #393939;
}

.footer a {
  color: #777;
  transition: color 0.3s;
}

.footer a:hover {
  color: white;
}

.footer__heading {
  text-transform: uppercase;
  font-weight: 600;
  padding-bottom: 2rem;
}

.footer__brand {
  text-align: center;
  margin-top: 5rem;
}

.footer__items {
  padding-bottom: 2rem;
}

.footer__copyright {
  color: white;
  opacity: 0.3;
  font-size: 2.17rem;
}

@media screen and (min-width: 768px) {
  .footer__sections {
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
  }
  .footer .collapsible__chevron {
    display: none;
  }

  .footer .collapsible__content {
    opacity: 1;
    max-height: 100%;
  }

  .footer__brand {
    order: -1;
    margin-top: 2rem;
  }

  .footer__brand img {
    /* TODO: considering adding this to all images */
    width: 100%;
    max-width: 230px;
  }

  .footer__copyright {
    font-size: 1.5rem;
  }

  .footer__section {
    border: 0;
  }

  .footer__heading {
    font-size: 1.6rem;
  }

  .footer__section .footer__list {
    margin-top: 0rem;
    font-size: 1.5rem;
  }
}
